<template>

	<view class="body">
		<view class="head">
			<view class="head_back" >
				<image src="@/static/img/back.png" mode="" @click="navigateBack()"></image>
			</view>
			<view class="head_center hei_38_bold">执业认证</view>
			<view class=" head_right hong_30">

			</view>
		</view>

		<view class="zi_body shiming_body">

			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					执业证号
				</view> <input type="text" value="" placeholder="请填写执业证号" class="qian_28" v-model="zhenghao" />
			</view>
			<view class="shiming_list hei_28 zhuanchang">
				<view class="shiming_list_left">
					专长领域
				</view> 
				<picker :range="zhuanchang_arry" mode='multiSelector' @change="zhuanchang_arryChange" >
							<view class="zhuanchang_xuan">{{zhuanchang}} <image src="@/static/lsimg/go_r.png" mode=""></image></view> 
						</picker>

			</view>
			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					职务
				</view> <input type="text" value="" placeholder="在律所承担的职务" class="qian_28" v-model="zhiwu" />
			</view>
			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					执业年份
				</view> <input type="text" value="" placeholder="执业年份，例如：2年" class="qian_28" v-model="nianfen" />
			</view>
			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					执业地区
				</view> 
				<pickerAddress @change="change" class='aaaaaa'><text class="dizhi ">{{dizhi}}</text> <image src="@/static/lsimg/go_r.png" mode=""></image></pickerAddress>
			</view>
			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					执业律所
				</view> <input type="text" value="" placeholder="请输入律所名称" class="qian_28" v-model="lvsuo" />
			</view>
			<view class="shiming_list hei_28">
				<view class="shiming_list_left">
					个人概括
				</view> <input type="text" value="" placeholder="请输入个人概括" class="qian_28" v-model="gaikuo" />
			</view>
	

			<view class="up">
				<view class=" up_zheng">
					<view :class="['up_box',zheng_img==''?'up_zheng_top': '']" @click="up_zheng">
						<image :src="zheng_img" mode=""></image>
					</view>
					<text class="qian_26">请上传您的执业证正面完整图片</text>
				</view>
			</view>

			<view class="renzheng_btn">

				<button type="" :class="['tijao bai_30',zhenghao==''||zhuanchang=='第一专长 第二专长 第三专长'|| zhiwu==''||nianfen==''||dizhi=='执业的地区'||lvsuo==''||gaikuo==''||zheng_img==''?'sty_dis':''] "  @click="tijiao" :disabled="zhenghao==''||zhuanchang=='第一专长 第二专长 第三专长'|| zhiwu==''||nianfen==''||dizhi=='执业的地区'||lvsuo==''||gaikuo==''||zheng_img==''">确认提交</button>
			</view>


		</view>




	</view>



</template>

<script>
	import pickerAddress from '@/components/pickerAddress/pickerAddress.vue'
	export default {

		data() {
			return {
				zhenghao: '',
				zhuanchang:'第一专长 第二专长 第三专长',
				zhiwu: '',
				nianfen: '',
				dizhi:'执业的地区',
				lvsuo: '',
				gaikuo: '',
				zheng_img: '',
				dis:true,
				zhuanchang_arry: [
					['婚姻家庭', '交通事故','刑事辩护','劳动工伤','房产纠纷','债权债务','合同事务','医疗纠纷','公司经营','损害赔偿', ],
					['婚姻家庭', '交通事故','刑事辩护','劳动工伤','房产纠纷','债权债务','合同事务','医疗纠纷','公司经营','损害赔偿', ],
					['婚姻家庭', '交通事故','刑事辩护','劳动工伤','房产纠纷','债权债务','合同事务','医疗纠纷','公司经营','损害赔偿', ]
				],
				
			}
		},
		components: {
			pickerAddress
		},
		created() {

		},
		onLoad(option) {


		},
		methods: {
			navigateBack() {
				uni.navigateBack()
			},
	      change(data) {
				console.log(data)
				this.dizhi = data.data.join('')
				console.log(data.data.join(''))
			},
			zhuanchang_arryChange(data){
				console.log(data.target.value[0])
				var yi=this.zhuanchang_arry[0][data.target.value[0]]
				var er=this.zhuanchang_arry[1][data.target.value[1]]
				var san=this.zhuanchang_arry[2][data.target.value[2]]
				
				this.zhuanchang=yi+'-'+er+'-'+san
				
			},
			up_zheng() {
				let that = this
				uni.chooseImage({
					success(res) {
						console.log(res)
						// that.zheng_img = res.tempFilePaths[0];
						console.log(that.zhiye_zhao)
						// that.urlTobase64(res.tempFilePaths[0])
						uni.request({
							url: res.tempFilePaths[0],
							method: 'GET',
							responseType: 'arraybuffer',
							success: ress => {
								let base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
								base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
								// console.log(base64)
								that.zheng_img = base64;
							}
						})
					}
				})
			},
		
			// 转化base64格式
			urlTobase64(url) {
				uni.request({
					url: url,
					method: 'GET',
					responseType: 'arraybuffer',
					success: ress => {
						let base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
						base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
						// console.log(base64)
					}
				})
			},
			radio(e) {
				this.danxuan = e
			},
			tijiao() {


				if (this.zhenghao == '') {
					uni.showToast({
						title: '请输入执业证号',
						duration: 2000,
						icon: "none"
					});
					return false
				} else if (this.zhuanchang=='第一专长 第二专长 第三专长') {
					uni.showToast({
						title: '请选择专长',
						duration: 2000,
						icon: "none"
					});
					return false
				} else if (this.zhiwu == '') {
					uni.showToast({
						title: '请输入职务',
						duration: 2000,
						icon: "none"
					});
					return false
				}  else if (this.nianfen == '') {
					uni.showToast({
						title: '请输入年份',
						duration: 2000,
						icon: "none"
					});
					return false
				} else if (this.nianfen == '') {
					uni.showToast({
						title: '请输入年份',
						duration: 2000,
						icon: "none"
					});
					return false
				}else if (this.dizhi == '执业的地区') {
					uni.showToast({
						title: '请选择地址',
						duration: 2000,
						icon: "none"
					});
					return false
				}else if (this.lvsuo == '') {
					uni.showToast({
						title: '请输入律所',
						duration: 2000,
						icon: "none"
					});
					return false
				}else if (this.gaikuo == '') {
					uni.showToast({
						title: '请输入概括',
						duration: 2000,
						icon: "none"
					});
					return false
				}else if (this.zheng_img == '') {
					uni.showToast({
						title: '请上传执业证',
						duration: 2000,
						icon: "none"
					});
					return false
				}

				console.log(this.zhenghao,this.zhuanchang,this.zhiwu,this.nianfen,this.dizhi,this.lvsuo,this.gaikuo,this.zheng_img,)


			}
		}
	}
</script>

<style>
	.head {
		border-bottom: 2rpx solid #E8E8E8;
	}

	.shiming_list {
		height: 100rpx;
		display: flex;
		align-items: center;
		border-bottom: 2rpx solid #E8E8E8;
		padding-left: 30rpx;
		padding-right: 30rpx;
	}

	.shiming_list input {
		width: 500rpx;
	}

	.shiming_list_left {
		width: 169rpx;
	}

	.zhiyezhao {
		justify-content: space-between;
	}

	.up {
		display: flex;
		justify-content: center;
		border-top: 20rpx solid #F8F8F8;
		padding-top: 30rpx;
		padding-left: 30rpx;
		padding-right: 30rpx;
	}

	.renzheng_btn {
		padding-left: 30rpx;
		padding-right: 30rpx;
		margin-bottom: 30rpx;
	}

	.up_zheng
	 {
		text-align: center;
	}


.up_box{
	width: 586rpx;
		height: 256rpx;
		
		border-radius: 3rpx;
	margin-bottom: 15rpx;
	border-radius: 3rpx;
	border: solid 1rpx #f4f4f4;
	background-color: #FFFFFF;
}
	.up_box image,
	.up_box image {
		width: 100%;
		height:  100%;
	}

	.up_zheng_top {
		background: url(@/static/lsimg/up_zhiye_bg.png) no-repeat;
		background-size: 100% 100%;
	}



	.tijao {
		height: 89rpx;
		background-color: #F43A51;
		border-radius: 44rpx;
		margin-top: 110rpx;
		line-height: 89rpx;
	}

	.nan {
		margin-right: 55rpx;
	}

	.shiming_list_right {
		display: flex;
		align-items: center;
	}

	.tx {
		width: 80rpx;
		height: 80rpx;
		border-radius: 100%;
	}

	.genghuan {
		width: 14rpx;
		height: 24rpx;
		margin-left: 12rpx;
	}
	.sty_dis{
		color: #FFFFFF;
		border: none;
		background-color: #999999;
	}
	.zhuanchang{
		
	}
	.zhuanchang_xuan,.aaaaaa{
		display: flex;
		align-items: center;
		width:520rpx;
		justify-content: space-between;
		color: #777777;
	}
	input{
		color: #777777 !important;
	}
	.zhuanchang image,.aaaaaa image{
			width: 13rpx;
			height: 23rpx;;
			position: absolute;
			right: 30rpx;
	}
	.dizhi{
		color: #777777;
	}
</style>
